import { useState, useEffect } from 'react';
import { createClient } from '@supabase/supabase-js';
import { Auth } from '@supabase/auth-ui-react';
import { ThemeSupa } from '@supabase/auth-ui-shared';
import { HashRouter as Router, Route, Routes, Link, useNavigate } from "react-router-dom";
import { Button } from 'reactstrap';
import { Home } from "./logout";
import { Home2 } from "./nullx";
import { Home3 } from "./whyit";
import { Home4 } from "./whyit2";


// Supabase クライアントの設定
export const adminSupabase = createClient(
    'https://ehgdexjxerdlgzycrwja.supabase.co',
    'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpc3MiOiJzdXBhYmFzZSIsInJlZiI6ImVoZ2RleGp4ZXJkbGd6eWNyd2phIiwicm9sZSI6InNlcnZpY2Vfcm9sZSIsImlhdCI6MTczMTEyOTczNCwiZXhwIjoyMDQ2NzA1NzM0fQ.DGBcWAACp7x5kGZL7ssWKbm46rWf-Rwq6MqyzfRmHZg'
);

export const supabase = createClient(
    'https://ehgdexjxerdlgzycrwja.supabase.co',
    'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpc3MiOiJzdXBhYmFzZSIsInJlZiI6ImVoZ2RleGp4ZXJkbGd6eWNyd2phIiwicm9sZSI6ImFub24iLCJpYXQiOjE3MzExMjk3MzQsImV4cCI6MjA0NjcwNTczNH0._CMyfHd5oXR9QaXat2AniKT1rlKpsc4mslZNMtdKzJU'
);

export default function App() {
    const [session, setSession] = useState(null);
    const [userName, setUserName] = useState('');
    const [showMessage, setShowMessage] = useState(null);
    const [showNameForm, setShowNameForm] = useState(false);
    const [myName, setMyName] = useState('');
    const [showDeleteConfirm, setShowDeleteConfirm] = useState(false);
    const [myTip, setMyTip] = useState(''); // 格言用の状態
    const navigate = useNavigate();

    useEffect(() => {
        // クエリパラメータからaccess_tokenを取得
        const urlParams = new URLSearchParams(window.location.search); // `?` 以降の部分を取得
        const accessToken = urlParams.get('access_token');

        if (accessToken) {
            // access_tokenが含まれている場合は、ここで処理を行う
            console.log("Access Token:", accessToken);
        }

        // Supabaseのセッションを設定
        supabase.auth.getSession().then(({ data: { session } }) => {
            setSession(session);
            if (session) {
                checkUserInKikTable(session.user.id);
            }
        });

        const { data: { subscription } } = supabase.auth.onAuthStateChange((_event, session) => {
            setSession(session);
            if (session) {
                checkUserInKikTable(session.user.id);
            }
        });

        return () => subscription.unsubscribe();
    }, []);

    const checkUserInKikTable = async (userId) => {
        const { data, error } = await supabase
            .from('kik')
            .select('id, myname, mytip, user_id')
            .eq('user_id', userId);

        if (error) {
            console.error("Error fetching user data:", error.message);
        } else {
            if (data.length > 1) {
                // 複数行が存在する場合、余分な行を削除
                const rowsToDelete = data.slice(1); // 最初の1行を除いて削除対象に
                for (const row of rowsToDelete) {
                    const { error: deleteError } = await supabase
                        .from('kik')
                        .delete()
                        .eq('id', row.id);
                    if (deleteError) {
                        console.error("Error deleting extra row:", deleteError.message);
                    }
                }
            }
            if (data.length === 0) {
                const { error: insertError } = await supabase
                    .from('kik')
                    .insert([{ user_id: userId }]);

                if (insertError) {
                    console.error("Error inserting user data:", insertError.message);
                }
                setShowNameForm(true);
            } else {
                const userRow = data[0];
                setMyName(userRow.myname);
                setMyTip(userRow.mytip || ''); // 格言があれば表示
                if (!userRow.myname) {
                    setShowNameForm(true);
                }
            }
        }
    };

    const handleLogout = async () => {
        await supabase.auth.signOut();
        setSession(null);
        setShowMessage("ログアウトが完了しました。<br/>3秒後にトップページに戻ります。");
        setTimeout(() => {
            setShowMessage(null);
            navigate('/');  // クエリパラメータなしでトップページにリダイレクト
        }, 3000);
    };

    const handleRegisterUserName = async () => {
        if (!userName) {
            alert("ユーザー名を入力してください");
            return;
        }

        if (userName.length > 20) {
            alert("ユーザー名は20文字以内で入力してください");
            return;
        }

        const user = session?.user;

        if (user) {
            const { data, error } = await supabase
                .from('kik')
                .select('id, myname, user_id')
                .eq('user_id', user.id)
                .single();

            if (error) {
                console.error("Error checking user data:", error.message);
            } else {
                if (data) {
                    const { error: updateError } = await supabase
                        .from('kik')
                        .update({ myname: userName })
                        .eq('id', data.id);

                    if (updateError) {
                        console.error("Error updating myname:", updateError.message);
                    } else {
                        setMyName(userName);
                        setShowNameForm(false);
                        // ユーザー名変更後、ページをリロード
                        window.location.reload();  // ページをリロードして変更を反映
                    }
                }
            }
        }
    };

    const handleSaveTip = async () => {
        const user = session?.user;

        if (user) {
            const { data, error } = await supabase
                .from('kik')
                .select('id, mytip')
                .eq('user_id', user.id)
                .single();

            if (error) {
                console.error("Error saving tip:", error.message);
            } else {
                const { error: updateError } = await supabase
                    .from('kik')
                    .update({ mytip: myTip })
                    .eq('id', data.id);

                if (updateError) {
                    console.error("Error updating mytip:", updateError.message);
                } else {
                    setShowMessage("格言を保存しました。<br/>3秒後にトップページに戻ります。");
                    setTimeout(() => {
                        setShowMessage(null);
                        navigate('/');  // 格言保存後、クエリパラメータなしでトップページにリダイレクト
                    }, 3000);
                }
            }
        }
    };

    const handleDeleteAccount = async (confirmation) => {
        if (confirmation === 'NO') {
            setShowDeleteConfirm(false);
            return;
        }

        if (session) {
            await supabase.auth.signOut();
            setShowMessage("アカウントの削除が完了しました。<br/>3秒後にログイン画面に戻ります。");
            setTimeout(() => {
                setShowMessage(null);
                navigate('/');  // アカウント削除後、クエリパラメータなしでトップページにリダイレクト
            }, 3000);
        }
    };

    if (showMessage) {
        return (
            <div
                dangerouslySetInnerHTML={{
                    __html: showMessage,
                }}
            ></div>
        );
    }

    if (!session) {
        return (
            <Auth
                supabaseClient={supabase}
                appearance={{ theme: ThemeSupa }}
                providers={['google', 'github']}
                redirectTo="/"  // ログイン後にクエリパラメータなしでリダイレクト
            />
        );
    } else {
        return (
            <Router>
                <div>
                    <Link to="/">Home</Link>
                    <br />
                    <Link to="/can">Why It?</Link>
                    <br />

                    {showNameForm && (
                        <div>
                            <h1>ユーザー名を登録してください</h1>
                            <input
                                type="text"
                                value={userName}
                                onChange={(e) => setUserName(e.target.value)}
                                maxLength={20}
                            />
                            <button onClick={handleRegisterUserName}>登録</button>
                            <button onClick={() => setShowNameForm(false)}>戻る</button>
                        </div>
                    )}

                    {showDeleteConfirm && (
                        <div>
                            <h1>アカウントを削除しますか？</h1>
                            <Button color="danger" onClick={() => handleDeleteAccount('YES')}>YES</Button>
                            <Button color="secondary" onClick={() => handleDeleteAccount('NO')}>NO</Button>
                        </div>
                    )}

                    {!showNameForm && !showDeleteConfirm && myName && (
                        <div>
                            <h1>ようこそ！{myName}さん</h1>
                            <Button color="danger" onClick={() => setShowDeleteConfirm(true)}>アカウント削除</Button>
                            <br /><Button color="primary" onClick={handleLogout}>ログアウトする</Button>

                            <br />
                            <Button color="secondary" onClick={() => setShowNameForm(true)}>ユーザー名変更</Button>

                            <div>
                                <h2>格言を残そう！</h2>
                                <textarea
                                    value={myTip}
                                    onChange={(e) => setMyTip(e.target.value)}
                                    maxLength={2000}
                                />
                                <Button color="primary" onClick={handleSaveTip}>保存</Button>
                            </div>
                        </div>
                    )}

                    <Routes>
                        <Route path='/logout' element={<Home />} />
                        <Route path='/*' element={<Home2 />} />
                        <Route path='/can' element={<Home3 />} />
                        <Route path='/' element={<Home4 />} />
                    </Routes>
                </div>
            </Router>
        );
    }
}
